@import url(https://fonts.googleapis.com/css?family=Comfortaa|Montserrat&display=swap&subset=latin-ext);
*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  --main-color: #0FA0FF;
  --dark-main-color: #005099;
  --light-main-color: lightblue;
  --dark-color: #333;
  --light-color: #F2FAFA;
}

body {
  font-family: 'Montserrat', sans-serif;
  color: var(--dark-color);
  background: var(--light-color);
}

h1 {
  font-family: 'Comfortaa', sans-serif;
  font-size: 2.5rem;
  line-height: 3.5rem;
}

h2 {
  font-family: 'Comfortaa', sans-serif;
  font-weight: normal;
}

h3 {
  position: relative;
  text-transform: uppercase;
  letter-spacing: 2px;
  padding-bottom: 3rem;
}

h3::first-letter {
  display: block;
  color: white;
  border-radius: 50%;
  font-size: 2rem;
  line-height: 2rem;
  padding: 0 .2rem 0 1rem;
}

h3::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 1rem;
  width: 2rem;
  height: 5rem;
  background: var(--main-color);
  z-index: -1;
}

h4 {
  font-family: 'Comfortaa', sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
}
.projectSection,
.aboutSection,
.contactSection {
  padding: 20px 0;
  min-height: 100vh;
  max-width: 100vw;
  overflow: hidden;
}


@media(min-width:1024px) {

  main {
    max-width: 1024px;
    margin: 0 auto;
    padding: 20px 0;
    box-shadow: 0px 0px 15px lightgray;
  }
}
header {
  position: relative;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: flex-end;
  min-height: 100vh;
  max-height: 100vh;
  max-width: 100vw;
  padding: 20px 0 30px 0;
}

.logoOut {
  position: absolute;
  top: 50%;
  left: 50%;
  background: var(--main-color);
  width: 200px;
  height: 200px;
  border-radius: 50%;
  -webkit-transform: translate(-50%, -50%) scale(1);
          transform: translate(-50%, -50%) scale(1);
  -webkit-animation: pulse 3.6s 1.5s ease-in-out both;
          animation: pulse 3.6s 1.5s ease-in-out both;
}

.logo {
  position: relative;
  min-width: 200px;
  width: 200px;
  min-height: 200px;
  height: 200px;
  background: var(--main-color);
  border: none;
  border-radius: 50%;
  -webkit-animation: enter 2s ease-in-out both;
          animation: enter 2s ease-in-out both;
}

.logoO {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 150px;
  height: 150px;
  border: 18px solid white;
  border-radius: 50%;
}

.logoA {
  position: relative;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  width: 80px;
  height: 80px;
}

.logoA::before,
.logoA::after {
  content: "";
  position: absolute;
  bottom: 0;
  -webkit-transform-origin: center center;
          transform-origin: center center;
  width: 40px;
  height: 130px;
  background: white;
  border-radius: 20px;
  border: 10px solid var(--main-color);
}

.logoA:before {
  left: calc(50%);
  -webkit-transform: rotate(-26deg);
          transform: rotate(-26deg);
}

.logoA:after {
  left: calc(50% - 40px);
  -webkit-transform: rotate(26deg);
          transform: rotate(26deg);
}

.small {
  position: absolute;
  top: 0;
  left: 50vw;
  right: 50vw;
  -webkit-transform: translate(-50%, -40%) scale(.2);
