.navigation {
  overflow: hidden;
}

.navigation::before {
  content: "";
  position: fixed;
  top: -50px;
  right: -50px;
  width: 100px;
  height: 100px;
  background: var(--main-color);
  border-radius: 50%;
  -webkit-transition: -webkit-transform .5s;
  transition: -webkit-transform .5s;
  transition: transform .5s;
  transition: transform .5s, -webkit-transform .5s;
  z-index: 1;
}

.naviBurger {
  position: fixed;
  top: -50px;
  right: -50px;
  display: flex;
  flex-flow: column;
  justify-content: flex-end;
  align-items: flex-start;
  width: 100px;
  height: 100px;
  padding: 0 0 50px 17px;
  background: var(--main-color);
  border-radius: 50%;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .5s ease-in-out;
  transition: .5s ease-in-out;
  z-index: 5;
  cursor: pointer;
}

.naviBurgerSpan {
  position: absolute;
  display: block;
  width: 25px;
  height: 3px;
  margin: 2px 0;
  border-radius: 2px;
  background: white;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  -webkit-transition: .25s ease-in-out;
  transition: .25s ease-in-out;
}

.naviBurgerSpan:nth-child(1) {
  bottom: 21px;
}

.naviBurgerSpan:nth-child(2),
.naviBurgerSpan:nth-child(3) {
  bottom: 28px;
  -webkit-transform: translateX(0%);
          transform: translateX(0%);
}

.naviBurgerSpan:nth-child(4) {
  bottom: 35px;
}


nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  position: fixed;
  height: 100vh;
  width: 100vw;
  padding: 15% 0;
  -webkit-transform: translate(100vw, -100vw);
          transform: translate(100vw, -100vw);
  opacity: 0;
  z-index: 2;
  -webkit-transition: opacity .5s, -webkit-transform .5s;
  transition: opacity .5s, -webkit-transform .5s;
  transition: transform .5s, opacity .5s;
  transition: transform .5s, opacity .5s, -webkit-transform .5s;
}

nav>* {
  padding: 10px;
  color: white;
}

.naviButton {
  display: inline-block;
  position: relative;
  padding: 5px 10px;
  background: transparent;
  color: white;
  font-size: 1.2rem;
  border: none;
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
}

.naviButton::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  -webkit-transform: translateX(-50%) scaleX(0);
          transform: translateX(-50%) scaleX(0);
  width: 100%;
  height: 2px;
  background-color: var(--dark-main-color);
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
}

.naviButton.current {
  color: var(--dark-main-color);
  font-weight: bold;
}

.naviButton:hover::before {
  -webkit-transform: translateX(-50%) scaleX(1);
          transform: translateX(-50%) scaleX(1);
}

.naviList {
  list-style: none;
}

.naviListButton {
  position: relative;
  display: block;
  margin: 0 auto;
  padding: 20px 10px 0;
  background: none;
  border: none;
  color: white;
  font-size: 1.5em;
  overflow: hidden;
  cursor: pointer;
  -webkit-transition: -webkit-transform .3s ease-in-out;
  transition: -webkit-transform .3s ease-in-out;
  transition: transform .3s ease-in-out;
  transition: transform .3s ease-in-out, -webkit-transform .3s ease-in-out;
}

.naviListButton::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  -webkit-transform: translateX(-50%) scaleX(0);
          transform: translateX(-50%) scaleX(0);
  width: 100%;
  height: 2px;
  background-color: var(--dark-main-color);
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
}

.naviListButton:hover::before {
  -webkit-transform: translateX(-50%) scaleX(.9);
          transform: translateX(-50%) scaleX(.9);
}

.scroll {
  position: fixed;
  bottom: -50px;
  right: -50px;
  display: flex;
  flex-flow: column;
  justify-content: flex-start;
  align-items: flex-start;
  background: var(--main-color);
  width: 100px;
  height: 100px;
  padding: 23px 23px;
  border: none;
  border-radius: 50%;
  color: white;
  font-size: 1rem;
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
  -webkit-transition: opacity .5s, -webkit-transform .3s;
  transition: opacity .5s, -webkit-transform .3s;
  transition: opacity .5s, transform .3s;
  transition: opacity .5s, transform .3s, -webkit-transform .3s;
  z-index: 2;
  cursor: pointer;
}

.hidden {
  opacity: 0;
  -webkit-transform: scale(0);
          transform: scale(0);
}

/* After activate */
.navigation.active::before {
  -webkit-transform: scale(30);
          transform: scale(30);
}

.active .naviBurgerSpan:nth-child(1) {
  opacity: 0;
  -webkit-transform: rotate(0deg) translateX(-100%);
          transform: rotate(0deg) translateX(-100%);
}

.active .naviBurgerSpan:nth-child(4) {
  opacity: 0;
  -webkit-transform: rotate(0deg) translateX(100%);
          transform: rotate(0deg) translateX(100%);
}


.active .naviBurgerSpan:nth-child(2) {
  -webkit-transform: rotate(135deg);
  transform: rotate(135deg);
}

.active .naviBurgerSpan:nth-child(3) {
  -webkit-transform: rotate(-135deg);
  transform: rotate(-135deg);
}


.active nav {
  -webkit-transform: translateY(0vh);
          transform: translateY(0vh);
  opacity: 1;
}

.active .scroll {
  z-index: 0;
}

@media(max-width:1023px) and (orientation:landscape) {
  nav {
    padding: 0;
  }

  .small {
    top: 5px;
    left: 25px;
  }

  .languageSelection {
    position: absolute;
    top: 0;
    left: 10vw;
  }

  .naviListButton {
    padding: 15px;
  }

}

@media(min-width:1024px) {

  .navigation::before {
    top: 0;
    left: 0;
    width: 100vw;
    height: 100px;
    max-height: 100px;
    background: var(--main-color);
    border-radius: 0 0 0 50px;
    -webkit-transform: translate(100%, -50%);
            transform: translate(100%, -50%);
    -webkit-transition: -webkit-transform .5s;
    transition: -webkit-transform .5s;
    transition: transform .5s;
    transition: transform .5s, -webkit-transform .5s;
    z-index: 1;
  }

  .naviHeader {
    display: none;
  }

  .small {
    top: 5px;
    left: 50px;
  }

  .languageSelection {
    position: absolute;
    top: 0;
    left: 150px;
  }

  .naviList {
    position: absolute;
    top: 0;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    width: 1024px;
    padding: 0;
  }

  .naviListButton {
    min-width: 150px;
    padding: 10px 10px 5px;
    -webkit-transition: .3s;
    transition: .3s;
  }

  /* After activate */

  .navigation.active::before {
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
  }

  .active nav {
    top: 0;
    left: 0;
    padding: 0;
    height: 50px;
    flex-direction: row;
    align-items: flex-end;
    justify-content: space-between;
  }

}
.projectArea {
  position: relative;
  background: var(--dark-main-color);
  margin: 30px 0;
  padding: 20px;
  min-height: 250px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  background-blend-mode: overlay;
  overflow: hidden;
}

.projectArea::before {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--light-color);
  -webkit-transition: -webkit-transform .4s ease-in-out;
  transition: -webkit-transform .4s ease-in-out;
  transition: transform .4s ease-in-out;
  transition: transform .4s ease-in-out, -webkit-transform .4s ease-in-out;
  z-index: 0;
}

.projectId {
  position: absolute;
  top: 20px;
  left: 60px;
  -webkit-transition: font-size .3s;
  transition: font-size .3s;

}

.projectId::before {
  content: "";
  display: block;
  position: absolute;
  top: 10px;
  left: -60px;
  width: 50px;
  height: 1px;
  background: var(--dark-color);
  -webkit-transition: -webkit-transform .3s;
  transition: -webkit-transform .3s;
  transition: transform .3s;
  transition: transform .3s, -webkit-transform .3s;
}

.projectTitle {
  position: absolute;
  top: 20px;
  left: 100px;
  margin-bottom: 1rem;
  -webkit-transition: .3s;
  transition: .3s;
}

.projectDescription {
  position: absolute;
  top: 3rem;
  left: 100px;
  margin-bottom: 1rem;
  padding: 20px 20px 150px 0;
  font-size: .9rem;
  -webkit-transition: .3s;
  transition: .3s;
}

.projectActions {
  position: absolute;
  bottom: 10px;
  left: 50%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 60%;
  height: 60px;
}

.projectAnchor {
  position: relative;
  display: inline-block;
  width: 70px;
  line-height: 2rem;
  border-bottom: 1px solid var(--dark-color);
  color: var(--dark-color);
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  -webkit-transition: .6s .3s;
  transition: .6s .3s;
}

.projectAnchor::before {
  content: "v";
  display: block;
  position: absolute;
  bottom: -17px;
  right: 0;
  -webkit-transform: rotate(-90deg) translateY(0);
          transform: rotate(-90deg) translateY(0);
  -webkit-transition: font-color .6s .3s, -webkit-transform .3s;
  transition: font-color .6s .3s, -webkit-transform .3s;
  transition: transform .3s, font-color .6s .3s;
  transition: transform .3s, font-color .6s .3s, -webkit-transform .3s;
}

.projectAnchor::after {
  content: "";
  position: absolute;
  display: block;
  bottom: -1px;
  left: 0;
  width: 70px;
  height: 1px;
  background: var(--dark-color);
  -webkit-transition: background-color .6s .3s, -webkit-transform .3s;
  transition: background-color .6s .3s, -webkit-transform .3s;
  transition: transform .3s, background-color .6s .3s;
  transition: transform .3s, background-color .6s .3s, -webkit-transform .3s;
}

.projectAnchor:hover::before {
  -webkit-transform: rotate(-90deg) translateY(30px);
          transform: rotate(-90deg) translateY(30px);
}

.projectAnchor:hover::after {
  -webkit-transform: translateX(40%);
          transform: translateX(40%);
}

.projectArea:hover::before {
  -webkit-transform: translateX(100%);
          transform: translateX(100%);
}

.projectArea:hover .projectAnchor,
.projectArea:hover .projectTitle,
.projectArea:hover .projectDescription {
  color: white;
}

.projectArea:hover .projectAnchor {
  border-bottom-color: white;
}

.projectArea:hover .projectId::before,
.projectArea:hover .projectAnchor::after {
  background: white;
}

.projectArea:hover .projectId::before {
  -webkit-transform: translateY(calc(3rem - 10px));
          transform: translateY(calc(3rem - 10px));
}

.projectArea:hover .projectId {
  font-size: 10rem;
  color: rgba(255, 255, 255, .25);
}

@media(min-width:1024px) {
.projectTitle {
  left: 10rem;
}

.projectDescription {
  left: 10rem;
  padding: 20px 10rem 150px 0;
}
}
.myPhoto {
  margin: 20px auto;
  width: 200px;
  height: 200px;
  background: url(/portfolio/static/media/AO.159fb93a.png) gray;
  background-size: cover;
  background-blend-mode: luminosity;
  border: 1px solid gray;
  border-radius: 50%;
}

.skills,
.introduction {
  width: 100%;
  min-height: 50px;
  margin: 30px 0;
  padding: 40px 40px;
}

.skills {
  text-align: center;
  background: var(--dark-main-color);
  color: var(--light-color);
}

.skillsHeader {
  padding: 1rem 0 4rem;
}

.skillset {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  padding: 2rem 0;
  font-size: 3rem;
}

.skillset>* {
  padding: 1rem;
}

.skillset span {
  position: absolute;
  top: -2rem;
  left: 50%;
  width: 100%;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  color: white;
  opacity: 0;
  font-size: 1rem;
  -webkit-transition: opacity .3s;
  transition: opacity .3s;
  background-color: var(--dark-main-color);
}

.skillset i:hover+span {
  opacity: 1;
}

.aboutMeIntro {
  text-align: justify;
  padding: 10px 0;
  text-indent: 1.5rem;
}

@media(min-width:1024px) {
  .skills,
.introduction {
  padding: 40px 10rem;
}

.introduction {
  line-height: 2;
}

.skillset>* {
  padding: 2rem;
}

}
.contactSection {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-self: center;
}

.socialmedia {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 100%;
  height: 5rem;
  font-size: 3rem;
}

.socialmedia a {
  color: var(--dark-color);
  -webkit-transition: color .3s ease-in-out;
  transition: color .3s ease-in-out;
}

.socialmedia a:hover {
  color: var(--main-color);
}

.contactHeader {
  padding: 40px 0 20px;
  text-align: center;
}

.contactForm {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  max-width: 100%;
  min-height: 400px;
  padding: 20px 0;
}

.contactForm label {
  display: block;
  padding: 20px 0 5px;
  width: 66%;
  font-size: .8rem;
}

.name,
.email,
.message {
  display: block;
  padding: 5px 10px 5px;
  min-width: 66%;
  max-width: 66%;
  border: 2px solid transparent;
  border-bottom: 2px solid var(--main-color);
  background: none;
  font-size: 1rem;
}

.message {
  border-color: var(--main-color);
}

.name:focus,
.email:focus,
.message:focus {
  background: var(--light-main-color);
  outline: none;
}

.error {
  color: red;
  font-size: .8rem;
}

.formButton {
  margin: 30px 0;
  padding: 10px 30px;
  border: 2px solid var(--main-color);
  color: var(--main-color);
  background: none;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 1px;
  -webkit-transition: .3s;
  transition: .3s;
  cursor: pointer;
}

.formButton:hover {
  background: var(--main-color);
  color: white;
}

.red,
.green {
  padding: 20px;
  border-radius: 10px;
  color: white;
  font-weight: bold;
  letter-spacing: 1px;
}

.red {
  background: darkred;
}

.green { 
  background: green;
}

@media(min-width:1024px) {
  .contactForm {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
  }

  .name,
  .email,
  .message {
    min-width: 46%;
    max-width: 46%;
  }

}
footer {
  padding: 20px;
  background: var(--main-color);
  color: white;
  text-align: center;
}

.love {
  color: var(--dark-main-color);
}
